<template>
  <div class="row titulo">
    <h1 class="col">Egresos</h1>
    <h1 class="col">{{getSumaFerney}}</h1>
  </div>
  <hr/>
  <div v-if="getEditEgresoFerney">
    <EditEgreso />
  </div>
  <div v-else>
    <div class="row">
      <FormEgreso class="col-sm-12 col-md-6 col-lg-3 col-xl-3 mt-5" />
      <TableEgreso class="col-sm-12 col-md-6 col-lg-9 col-xl-9 mt-3" />
    </div>
  </div>
</template>

<script>
import { defineAsyncComponent } from 'vue'
import { mapGetters } from 'vuex'

export default {

  components: {
      FormEgreso: defineAsyncComponent(() => import("@/modules/Ferney/components/EgresoFerney/FormEgreso")),
      TableEgreso: defineAsyncComponent(() => import("@/modules/Ferney/components/EgresoFerney/TableEgreso")),
      EditEgreso: defineAsyncComponent(() => import("@/modules/Ferney/components/EgresoFerney/EditEgreso")),
  },
  computed:{
    ...mapGetters('ferneyEgreso',['getSumaFerney', 'getEditEgresoFerney'])
  }
}
</script>

<style scoped>
  .titulo{
    text-align: center;
  }

</style>